@font-face {
  font-family: NordRegular;
  src: url("Nord-Regular.262c0118.otf") format("opentype");
}

@font-face {
  font-family: SudRegular;
  src: url("Sud-Regular.37ce8ce3.otf") format("opentype");
}

@font-face {
  font-family: SentinelBook;
  src: url("Sentinel-Book.ad958f32.otf") format("opentype");
}

@font-face {
  font-family: SentinelBold;
  src: url("Sentinel-Bold.734b14c3.otf") format("opentype");
}

@font-face {
  font-family: SentinelLight;
  src: url("Sentinel-Light.b9fb161e.otf") format("opentype");
}

@font-face {
  font-family: SentinelMedium;
  src: url("Sentinel-Medium.726805ae.otf") format("opentype");
}

.preload * {
  transition: none !important;
}

* {
  border: none;
  outline: none;
  margin: 0;
  padding: 0;
}

*, :before, :after {
  box-sizing: border-box;
}

html {
  font-size: 100%;
}

.wf-loading .tk-fade {
  opacity: 0;
  transition: opacity 1s;
}

.wf-active .tk-fade {
  opacity: 1;
}

body {
  color: #000;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  -webkit-text-size-adjust: none;
  -moz-text-size-adjust: none;
  -ms-text-size-adjust: none;
  -webkit-tap-highlight-color: #0000;
  background: #fff;
  width: 100%;
  font-family: SentinelLight, sans-serif;
  font-size: 1rem;
  font-style: normal;
  font-weight: 400;
}

body.masonry {
  overflow-y: scroll;
}

a {
  color: inherit;
  text-decoration: underline;
}

h1, h2, h3, p {
  margin-bottom: 20px;
  line-height: 1.4;
}

h1:last-child, h2:last-child, h3:last-child, p:last-child {
  margin-bottom: 0;
}

@media (width <= 767px) and (orientation: portrait) {
  p {
    font-size: 1.1rem;
    line-height: 1.5;
  }
}

h1, h2, .headline {
  font-family: NordRegular, sans-serif;
}

h3 {
  font-family: SudRegular, sans-serif;
}

em, i {
  font-style: italic;
}

label, input, textarea, select {
  font-size: 16px;
  display: block;
}

input[type="checkbox"], input[type="radio"] {
  display: inline-block;
}

input, textarea {
  color: #000;
  box-sizing: border-box;
  appearance: none;
  background-color: #0000;
  border: 1px solid #000;
  border-radius: 0;
  height: 34px;
  padding: 0 10px;
}

input[type="submit"] {
  cursor: pointer;
  box-sizing: border-box;
  appearance: none;
  border: 0;
  border-radius: 0;
  padding: 0 10px;
}

label {
  padding-bottom: 5px;
}

.cell .mediawrap {
  width: 100%;
  height: 0;
  position: relative;
}

.cell .mediawrap img.photo, .cell .mediawrap video {
  object-fit: cover;
  width: 100%;
  height: 100%;
  display: block;
  position: absolute;
  top: 0;
  left: 0;
}

.openLightbox, .openLightboxSingle {
  cursor: pointer;
}

.cover {
  opacity: 0;
  background-position: 50%;
  background-repeat: no-repeat;
  background-size: cover;
}

.cover.loaded {
  opacity: 1;
  transition: opacity .3s;
}

.loadme, .loadmeview, .bg-loadme {
  opacity: 0;
}

.loadme.loaded, .loadmeview.loaded, .bg-loadme.loaded {
  opacity: 1;
  transition: opacity .3s;
}

.photo.size-width {
  width: 102%;
  height: auto;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.photo.size-height {
  width: auto;
  height: 102%;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.resize-image {
  overflow: hidden;
}

.social ul li {
  opacity: 0;
  transition: opacity .3s;
}

.social ul li.loaded {
  opacity: 1;
}

.text-block img, .text-block video {
  max-width: 100%;
  height: auto;
  display: block;
}

.text-block .cols {
  flex-wrap: wrap;
  justify-content: space-between;
  align-items: flex-start;
  display: flex;
}

.text-block .cols .col {
  display: block;
}

h1 {
  font-size: 3.4vw;
  line-height: 1.3;
}

h2 {
  font-size: 3rem;
}

h3 {
  letter-spacing: 1.5px;
  font-size: 1.2rem;
}

p {
  font-size: 1rem;
}

strong, b {
  font-family: SentinelBold, sans-serif;
  font-weight: 400;
}

.fadeOn {
  opacity: 0;
}

.fadeUp, .fadeUpDelay {
  opacity: 0;
  transform: translateY(1rem);
}

main {
  z-index: 1;
  margin: 0 auto;
  position: relative;
}

main article {
  min-height: calc(100vh - 127px);
}

main #content:before {
  content: "";
  height: 140px;
  display: block;
}

.homepage main #content:before {
  height: 100px;
}

.textpage {
  background-color: #f7da70;
}

@media (width <= 767px) and (orientation: portrait) {
  main #content:before {
    height: 150px;
  }
}

header {
  z-index: 104;
  pointer-events: none;
  width: 100%;
  height: 140px;
  position: fixed;
  top: 0;
  left: 0;
}

header .inner {
  z-index: 15;
  width: 100%;
  height: 100%;
  position: absolute;
}

header .inner .logo {
  pointer-events: all;
  z-index: 104;
  width: 210px;
  height: 83px;
  position: absolute;
  top: 35px;
  left: 4%;
}

header .inner .logo img, header .inner .logo svg {
  width: 100%;
  height: auto;
  display: block;
  position: absolute;
  overflow: visible;
}

header .inner .logo img *, header .inner .logo svg * {
  fill: #000;
}

header .inner .logo svg:first-child {
  display: none;
}

header .inner .menuBtn {
  cursor: pointer;
  z-index: 3;
  pointer-events: all;
  width: 115px;
  height: 115px;
  transition: opacity .3s;
  position: absolute;
  top: 0;
  right: 0;
}

header .inner .menuBtn svg.arrow_down {
  transition: transform .3s ease-out;
  position: absolute;
  top: 0;
  right: 0;
  transform: translate(30px, -30px);
}

header .inner .menuBtn svg.arrow_down * {
  fill: #f15a29;
}

header .inner .menuBtn svg.arrow_up {
  transform-origin: 100% 0;
  width: 80%;
  height: auto;
  transition: transform .3s ease-out;
  display: none;
  position: absolute;
  top: 0;
  right: 0;
}

header .inner .menuBtn svg.arrow_up * {
  fill: #000;
}

header .inner .menuBtn:hover svg.arrow_down {
  transform: translate(10px, -10px);
}

header .inner .menuBtn:hover svg.arrow_up {
  transform: scale(.9);
}

header nav {
  z-index: 10;
  pointer-events: none;
  opacity: 0;
  background-color: #f15a29e6;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 100%;
  transition: opacity .3s;
  display: flex;
  position: fixed;
  top: 0;
  left: 0;
}

header nav ul {
  z-index: 15;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  list-style-type: none;
  display: flex;
  position: relative;
}

header nav ul li a {
  color: #000;
  letter-spacing: 12px;
  padding: 5px 0;
  font-family: SudRegular, sans-serif;
  font-size: 4vw;
  font-weight: 400;
  text-decoration: none;
  transition: color .3s;
  display: flex;
}

header nav ul li a.inactive {
  color: #ca9f53;
}

header nav .bottom_left {
  font-family: SentinelLight, sans-serif;
  font-size: 1.1rem;
  font-weight: 400;
  position: absolute;
  bottom: 20px;
  left: 40px;
}

header nav .bottom_left a {
  color: #000;
  font-family: SentinelBold, sans-serif;
  font-weight: 400;
  text-decoration: none;
  transition: color .2s;
}

header nav .bottom_left a:hover {
  color: #ca9f53;
}

header nav .bottom_right {
  font-family: SentinelLight, sans-serif;
  font-size: 1.1rem;
  font-weight: 400;
  position: absolute;
  bottom: 20px;
  right: 60px;
}

header nav .bottom_right a {
  color: #000;
  font-family: SentinelBold, sans-serif;
  font-weight: 400;
  text-decoration: none;
  transition: color .2s;
}

header nav .bottom_right a:hover {
  color: #ca9f53;
}

@media (width <= 767px) and (orientation: portrait) {
  header .inner .logo {
    width: 175px;
    top: 25px;
    left: 5%;
  }

  header .inner .menuBtn {
    width: 80px;
    height: 80px;
  }

  header .inner .menuBtn svg.arrow_down, header .inner .menuBtn:hover svg.arrow_down {
    transform: translate(15px, -15px);
  }

  header nav {
    background-color: #f15a29f2;
  }

  header nav ul li a {
    font-size: 6vw;
  }

  header nav .bottom_left {
    left: 5%;
  }

  header nav .bottom_right {
    right: 5%;
  }
}

.lightboxOpen header .inner .menuBtn {
  opacity: 0;
  pointer-events: none;
}

.lightboxOpen header .inner .logo {
  pointer-events: none;
}

.menuOn header .inner .menuBtn svg.arrow_down {
  display: none;
}

.menuOn header .inner .menuBtn svg.arrow_up {
  display: block;
}

.menuOn header nav {
  opacity: 1;
  pointer-events: all;
}

.contact_cover {
  opacity: 0;
  pointer-events: none;
  z-index: 100;
  background-color: #ffffffe6;
  width: 100%;
  height: 100%;
  position: fixed;
  top: 0;
  left: 0;
}

.contactOpen header nav {
  opacity: 0;
  pointer-events: none;
}

.contactOpen header nav ul {
  pointer-events: none;
}

.contactOpen .contact_cover {
  opacity: 1;
  pointer-events: all;
  transition: opacity .3s;
}

@media (width <= 767px) and (orientation: portrait) {
  .contact_cover {
    display: none;
  }
}

.bio_module {
  z-index: 101;
  width: 100vw;
  height: auto;
  transition: transform .75s cubic-bezier(.9, .49, .35, 1.2);
  position: fixed;
  overflow-y: scroll;
  transform: translateY(0);
}

.bio_module.hidden {
  transform: translateY(-100%);
}

.bio_module.hidden .inner {
  background-color: #0000;
  transition: background-color;
}

.bio_module.scroll {
  height: 100vh;
}

.bio_module .col_wrap {
  height: auto;
  padding-top: 140px !important;
  padding-bottom: 0 !important;
}

.bio_module .bottom {
  justify-content: space-between;
  align-items: center;
  width: calc(100% - 80px);
  margin: 0 auto 20px;
  display: flex;
}

.bio_module .bottom .bottom_left {
  z-index: 4;
  font-family: SentinelLight, sans-serif;
  font-size: 1.1rem;
  font-weight: 400;
}

.bio_module .bottom .bottom_left a {
  color: #000;
  font-family: SentinelBold, sans-serif;
  font-weight: 400;
  text-decoration: none;
  transition: opacity .2s;
}

.bio_module .bottom .bottom_left a:hover {
  opacity: .5;
}

.bio_module .bottom .bottom_right {
  z-index: 4;
  font-family: SentinelLight, sans-serif;
  font-size: 1.1rem;
  font-weight: 400;
}

.bio_module .bottom .bottom_right a {
  color: #000;
  font-family: SentinelBold, sans-serif;
  font-weight: 400;
  text-decoration: none;
  transition: color .2s;
}

.bio_module .bottom .bottom_right a:hover {
  color: #ca9f53;
}

.contact_module {
  z-index: 101;
  width: 100vw;
  height: 100vh;
  transition: transform .75s cubic-bezier(.9, .49, .35, 1.2);
  position: fixed;
  transform: translateY(0);
}

.contact_module .bottom {
  display: none;
}

.contact_module .bottom_left {
  z-index: 4;
  font-family: SentinelLight, sans-serif;
  font-size: 1.1rem;
  font-weight: 400;
  position: absolute;
  bottom: 20px;
  left: 40px;
}

.contact_module .bottom_left a {
  color: #000;
  font-family: SentinelBold, sans-serif;
  font-weight: 400;
  text-decoration: none;
  transition: opacity .2s;
}

.contact_module .bottom_left a:hover {
  opacity: .5;
}

.contact_module .bottom_right {
  z-index: 4;
  font-family: SentinelLight, sans-serif;
  font-size: 1.1rem;
  font-weight: 400;
  position: absolute;
  bottom: 20px;
  right: 60px;
}

.contact_module .bottom_right a {
  color: #000;
  font-family: SentinelBold, sans-serif;
  font-weight: 400;
  text-decoration: none;
  transition: color .2s;
}

.contact_module .bottom_right a:hover {
  color: #ca9f53;
}

.contact_module .background {
  pointer-events: none;
  z-index: 1;
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0;
  left: 0;
}

.contact_module .background .block {
  width: 33.33%;
  height: 100%;
  position: absolute;
  top: 0;
}

.contact_module .background .block:first-child {
  background-color: #c5c5c5fa;
  left: 0;
}

.contact_module .background .block:nth-child(2) {
  background-color: #f7da70fa;
  left: 33.33%;
}

.contact_module .background .block:last-child {
  background-color: #f6eddafa;
  left: 66.66%;
}

.contact_module a {
  opacity: 1;
  text-decoration: none;
  transition: opacity .2s;
}

.contact_module a:hover {
  opacity: .5;
}

.contact_module.hidden {
  transform: translateY(-100%);
}

.contact_module.hidden .inner {
  background-color: #0000;
  transition: background-color;
}

.contact_module .inner {
  z-index: 2;
  height: 100%;
  position: relative;
}

.contact_module .closeBtn {
  z-index: 101;
}

.contact_module .closeBtn p {
  letter-spacing: 1.5px;
  writing-mode: vertical-lr;
  cursor: pointer;
  font-family: SudRegular, sans-serif;
  font-size: .8rem;
  font-weight: 600;
  text-decoration: underline;
  position: absolute;
  top: 45px;
  right: 50px;
}

.contact_module table {
  background-color: #0000;
}

.contact_module table tbody tr:first-child td {
  padding-top: 200px;
}

.contact_module table tbody tr td {
  padding: 26px 6% 0 4%;
}

.contact_module table tbody tr td h2 {
  border-bottom: 1px solid #000;
  width: 90%;
  margin-bottom: 20px;
  padding-bottom: 5px;
  font-family: SudRegular, sans-serif;
  font-size: 1.8rem;
  font-weight: 400;
}

.contact_module table tbody tr td h3 {
  border-bottom: 1px solid #000;
  margin-bottom: 6px;
  padding-bottom: 0;
  font-size: .7rem;
  font-weight: 600;
  display: inline-block;
}

.contact_module table tbody tr td p {
  line-height: 1.7;
}

@media (width <= 767px) and (orientation: portrait) {
  .bio_module {
    transition: transform 1.5s cubic-bezier(.9, .49, .35, 1.2);
  }

  .bio_module .bottom {
    width: 90%;
    margin: 0 auto 100px;
  }

  .contact_module {
    z-index: 14;
    background-color: #c5c4c2;
    height: 100vh;
    overflow-y: scroll;
  }

  .contact_module .bottom_left.desktop, .contact_module .bottom_right.desktop {
    display: none;
  }

  .contact_module .bottom {
    background-color: #f6edda;
    justify-content: space-between;
    align-items: center;
    width: 100%;
    margin: 0 auto;
    padding: 0 0 100px;
    display: flex;
  }

  .contact_module .bottom .bottom_left {
    left: unset;
    bottom: unset;
    margin-left: 5%;
    display: block;
    position: relative;
  }

  .contact_module .bottom .bottom_right {
    right: unset;
    bottom: unset;
    margin-right: 5%;
    display: block;
    position: relative;
  }

  .contact_module .background {
    display: none;
  }

  .contact_module .closeBtn {
    display: none;
    transform: translate(30px, -20px);
  }

  .contact_module table {
    grid-template-columns: 1fr;
    display: grid;
    height: auto !important;
  }

  .contact_module table colgroup col {
    width: 100% !important;
  }

  .contact_module table tbody, .contact_module table tbody tr {
    display: contents;
  }

  .contact_module table tbody tr:first-child td {
    padding-top: 50px;
  }

  .contact_module table tbody tr h3, .contact_module table tbody tr p {
    width: 100% !important;
  }

  .contact_module table tbody tr td {
    padding: 26px 6% 0;
  }

  .contact_module table tbody tr td h2 {
    margin-bottom: 0;
  }

  .contact_module table tbody tr td:first-child {
    background-color: #c5c4c2;
  }

  .contact_module table tbody tr td:nth-child(2) {
    background-color: #f7da70;
  }

  .contact_module table tbody tr td:last-child {
    background-color: #f6edda;
  }

  .contact_module table tbody tr:first-child td:first-child {
    grid-area: 1 / 1;
    padding-top: 175px;
  }

  .contact_module table tbody tr:first-child td:nth-child(2) {
    grid-area: 4 / 1;
  }

  .contact_module table tbody tr:first-child td:nth-child(3) {
    grid-area: 7 / 1;
  }

  .contact_module table tbody tr:nth-child(2) td:first-child {
    grid-area: 2 / 1;
    padding-bottom: 50px;
  }

  .contact_module table tbody tr:nth-child(2) td:nth-child(2) {
    grid-area: 5 / 1;
    padding-bottom: 50px;
  }

  .contact_module table tbody tr:nth-child(2) td:nth-child(3) {
    grid-area: 8 / 1;
    padding-bottom: 50px;
  }

  .contact_module table tbody tr:nth-child(3) td:first-child {
    grid-area: 3 / 1;
  }

  .contact_module table tbody tr:nth-child(3) td:nth-child(2) {
    grid-area: 6 / 1;
    padding-bottom: 0;
  }

  .contact_module table tbody tr:nth-child(3) td:nth-child(3) {
    grid-area: 9 / 1;
  }

  .contactOpen header nav ul li:last-child {
    opacity: 0;
    pointer-events: none;
  }

  .contactOpen .closeBtn {
    display: block;
  }
}

footer, .footer {
  z-index: 5;
  color: #000;
  position: relative;
}

footer .inner, .footer .inner {
  width: 92%;
  margin: auto;
  padding: 45px 0;
  font-size: 0;
  line-height: 0;
  overflow: hidden;
}

footer .inner a, .footer .inner a {
  text-decoration: none;
}

footer .inner .animated_name, .footer .inner .animated_name {
  flex-wrap: wrap;
  align-items: flex-start;
  width: 100%;
  height: 120px;
  margin: auto auto 25px;
  display: flex;
  position: relative;
}

footer .inner .animated_name .mask, .footer .inner .animated_name .mask {
  z-index: 5;
  clip-path: inset(0);
  background-color: #fff;
  width: calc(100% - 460px);
  height: 140px;
  position: absolute;
  bottom: -11px;
  left: 244px;
}

footer .inner .animated_name img, .footer .inner .animated_name img {
  clip-path: inset(0 77% 0 0);
  height: 120px;
  position: absolute;
  bottom: 0;
  left: 0;
}

footer .inner .animated_name svg#joe, .footer .inner .animated_name svg#joe {
  width: auto;
  height: 120px;
  position: absolute;
  top: 0;
  left: 0;
}

footer .inner .animated_name svg#joe path, footer .inner .animated_name svg#joe rect, .footer .inner .animated_name svg#joe path, .footer .inner .animated_name svg#joe rect {
  fill: currentColor;
