footer .inner .animated_name .first_name, .footer .inner .animated_name .first_name {
  z-index: 5;
  display: flex;
  position: relative;
}

footer .inner .animated_name .first_name .lines, .footer .inner .animated_name .first_name .lines {
  width: 100%;
  height: 137px;
  position: relative;
}

footer .inner .animated_name .first_name .lines .line, .footer .inner .animated_name .first_name .lines .line {
  background-color: #000;
  width: 100%;
  height: 13px;
  position: absolute;
}

footer .inner .animated_name .first_name .lines .line:first-child, .footer .inner .animated_name .first_name .lines .line:first-child {
  top: 18px;
  left: 0;
}

footer .inner .animated_name .first_name .lines .line:nth-child(2), .footer .inner .animated_name .first_name .lines .line:nth-child(2) {
  height: 12px;
  top: 71px;
  left: -5px;
