footer .inner .animated_name .first_name .lines .line:nth-child(3), .footer .inner .animated_name .first_name .lines .line:nth-child(3) {
  height: 12px;
  top: 127px;
  left: 0;
}

footer .inner .animated_name .last_name, .footer .inner .animated_name .last_name {
  z-index: 10;
  width: 100vw;
  display: flex;
  position: absolute;
  bottom: 0;
  left: 60%;
}

footer .inner .animated_name .last_name .spacer, .footer .inner .animated_name .last_name .spacer {
  width: 60px;
  height: 100%;
  position: absolute;
  top: 0;
  left: -60px;
}

footer .inner .animated_name .last_name svg, .footer .inner .animated_name .last_name svg {
  width: auto;
  height: 120px;
}

footer .inner .animated_name .last_name svg#liese, .footer .inner .animated_name .last_name svg#liese {
  margin-left: 20px;
}

footer .bottom, .footer .bottom {
  border-top: 1px solid #000;
  width: 100%;
  padding-top: 20px;
  display: flex;
  position: relative;
}

footer .bottom .col, .footer .bottom .col {
  flex-direction: column;
  justify-content: flex-start;
